@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  text-align: center;
}

.App-header {
  font-size: calc(5px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.lazy {
  animation: mymove 1.8s infinite;
}

@keyframes mymove {
  0% {
    background-color: #323841;
  }
  50% {
    background-color: #525861;
  }
  100% {
    background-color: #323841;
  }
}